html,
body {
  height: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  color: #fff;
  background-color: #262626;
}

input,
button {
  outline: none;
  background-color: transparent;
  border: none;
  font-family: inherit;
}

button {
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

p {
  margin: 0;
}

input[type='checkbox'] {
  margin: 0;
}

abbr[title] {
  text-decoration: none;
}
